*{
  box-sizing: border-box;
}

body{
  width: 100  vw;
  margin: 0px;
  overflow-x: hidden;
}

h1,h2,h3,h4,p{
  margin: 0px;
  font-size: 1rem;
  font-weight: normal;
}

a{
  text-decoration: none;
  color:inherit;
  display: block;
}

img{
  display: block;
  max-width: 100%;
}
